// Ajuda / Manual do Sistema - Estilos

$primary-blue: #4680FF;
$gold: #C5A55A;
$dark-blue: #1B4965;
$bg-light: #F5F7FA;
$bg-card: #FFFFFF;
$text-primary: #2D3436;
$text-secondary: #636E72;
$border-light: #E9ECEF;

.ajuda-wrapper {
  // ─── Header ─────────────────────────────────────────
  .ajuda-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
    flex-wrap: wrap;
    gap: 16px;

    .ajuda-title {
      h4 {
        font-size: 20px;
        font-weight: 700;
        color: $text-primary;
        margin-bottom: 4px;
      }

      p {
        font-size: 13px;
        color: $text-secondary;
        margin: 0;
      }
    }

    .ajuda-search {
      position: relative;
      min-width: 260px;

      input {
        width: 100%;
        border: 1.5px solid $border-light;
        border-radius: 20px;
        padding: 8px 16px 8px 38px;
        font-size: 13px;
        transition: all 0.2s ease;
        background: $bg-card;

        &:focus {
          outline: none;
          border-color: $primary-blue;
          box-shadow: 0 0 0 3px rgba($primary-blue, 0.1);
        }

        &::placeholder { color: #A0AEC0; }
      }

      .search-icon {
        position: absolute;
        left: 14px;
        top: 50%;
        transform: translateY(-50%);
        color: #A0AEC0;
        font-size: 13px;
      }
    }
  }

  // ─── Layout ─────────────────────────────────────────
  .ajuda-content {
    display: flex;
    gap: 24px;
    min-height: 500px;
  }

  // ─── Sidebar / Índice ───────────────────────────────
  .ajuda-sidebar {
    width: 240px;
    min-width: 240px;
    background: $bg-card;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
    padding: 16px 0;
    height: fit-content;
    position: sticky;
    top: 100px;

    .sidebar-title {
      font-size: 11px;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.8px;
      color: $text-secondary;
      padding: 0 20px 12px;
      border-bottom: 1px solid $border-light;
      margin-bottom: 8px;
    }

    .sidebar-item {
      display: flex;
      align-items: center;
      gap: 10px;
      padding: 10px 20px;
      cursor: pointer;
      transition: all 0.2s ease;
      border-left: 3px solid transparent;
      font-size: 13px;
      font-weight: 500;
      color: $text-secondary;

      &:hover {
        background: $bg-light;
        color: $text-primary;
      }

      &.active {
        background: rgba($primary-blue, 0.05);
        color: $primary-blue;
        border-left-color: $primary-blue;
        font-weight: 600;
      }

      .sidebar-icon {
        width: 28px;
        height: 28px;
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        flex-shrink: 0;
      }
    }
  }

  // ─── Painel de Conteúdo ─────────────────────────────
  .ajuda-panel {
    flex: 1;
    background: $bg-card;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
    padding: 28px 32px;
    animation: fadeInPanel 0.3s ease-out;

    .panel-header {
      display: flex;
      align-items: center;
      gap: 14px;
      margin-bottom: 8px;

      .panel-icon {
        width: 44px;
        height: 44px;
        border-radius: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        flex-shrink: 0;
      }

      h5 {
        font-size: 18px;
        font-weight: 700;
        color: $text-primary;
        margin: 0;
      }
    }

    .panel-description {
      font-size: 14px;
      color: $text-secondary;
      margin-bottom: 24px;
      padding-bottom: 16px;
      border-bottom: 2px solid $bg-light;
      line-height: 1.6;
    }

    // Acordeão
    .subsection-list {
      .subsection-item {
        border: 1.5px solid $border-light;
        border-radius: 10px;
        margin-bottom: 10px;
        overflow: hidden;
        transition: all 0.2s ease;

        &:hover {
          border-color: rgba($primary-blue, 0.2);
        }

        &.open {
          border-color: rgba($primary-blue, 0.3);
          box-shadow: 0 2px 8px rgba($primary-blue, 0.06);
        }
      }

      .subsection-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 14px 18px;
        cursor: pointer;
        transition: all 0.2s ease;
        background: transparent;
        border: none;
        width: 100%;
        text-align: left;

        &:hover {
          background: $bg-light;
        }

        .subsection-title {
          font-size: 14px;
          font-weight: 600;
          color: $text-primary;
          display: flex;
          align-items: center;
          gap: 10px;

          .subsection-number {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: $bg-light;
            color: $text-secondary;
            font-size: 11px;
            font-weight: 700;
            flex-shrink: 0;
          }
        }

        .chevron {
          color: $text-secondary;
          font-size: 12px;
          transition: transform 0.25s ease;

          &.open {
            transform: rotate(180deg);
          }
        }
      }

      .subsection-content {
        padding: 0 18px 16px 52px;
        font-size: 13px;
        color: $text-secondary;
        line-height: 1.7;
        animation: fadeInContent 0.25s ease-out;
      }
    }
  }

  // ─── Empty / No Results ─────────────────────────────
  .ajuda-no-results {
    text-align: center;
    padding: 60px 20px;
    color: $text-secondary;

    i {
      font-size: 40px;
      color: #CBD5E0;
      margin-bottom: 14px;
      display: block;
    }

    p { font-size: 14px; }
  }

  // ─── Footer ─────────────────────────────────────────
  .ajuda-footer {
    text-align: center;
    margin-top: 24px;
    padding-top: 16px;
    font-size: 12px;
    color: lighten($text-secondary, 15%);
  }
}

// ─── Animações ────────────────────────────────────────
@keyframes fadeInPanel {
  from { opacity: 0; transform: translateX(8px); }
  to { opacity: 1; transform: translateX(0); }
}

@keyframes fadeInContent {
  from { opacity: 0; transform: translateY(-4px); }
  to { opacity: 1; transform: translateY(0); }
}

// ─── Responsivo ───────────────────────────────────────
@media (max-width: 768px) {
  .ajuda-wrapper {
    .ajuda-content {
      flex-direction: column;
    }

    .ajuda-sidebar {
      width: 100%;
      min-width: 100%;
      position: static;
      display: flex;
      flex-wrap: wrap;
      padding: 12px;
      gap: 4px;

      .sidebar-title {
        width: 100%;
        padding: 0 8px 8px;
      }

      .sidebar-item {
        padding: 8px 12px;
        border-left: none;
        border-radius: 8px;
        font-size: 12px;

        &.active {
          border-left: none;
          background: rgba($primary-blue, 0.1);
        }

        .sidebar-icon {
          width: 24px;
          height: 24px;
          font-size: 10px;
        }
      }
    }

    .ajuda-panel {
      padding: 20px;
    }

    .ajuda-header .ajuda-search {
      min-width: 100%;
    }
  }
}
